export default {
    convert: convertRmb,
    rmbToNum:rmbToNum
}
function convertRmb(money) {
    var cnNums = new Array("", "", "", "", "", "", "", "", "", ""); //  
    var cnIntRadice = new Array("", "", "", ""); //  
    var cnIntUnits = new Array("", "", "", ""); //  
    var cnDecUnits = new Array("", "", "", ""); //  
    var cnInteger = ""; //  
    var cnIntLast = ""; //  
    var maxNum = 999999999999999.9999; //  

    var IntegerNum; //  
    var DecimalNum; //  
    var ChineseStr = ""; //  
    var parts; //  
    if (money == "") {
        return "";
    }
    money = parseFloat(money);
    if (money >= maxNum) {
        $.alert('');
        return "";
    }
    if (money == 0) {
        ChineseStr = cnNums[0] + cnIntLast
        return ChineseStr;
    }
    money = money.toString(); //  
    if (money.indexOf(".") == -1) {
        IntegerNum = money;
        DecimalNum = '';
    } else {
        parts = money.split(".");
        IntegerNum = parts[0];
        DecimalNum = parts[1].substr(0, 4);
    }
    if (parseInt(IntegerNum, 10) > 0) {//  
        var zeroCount = 0;
        var IntLen = IntegerNum.length;
        for (var i = 0; i < IntLen; i++) {
           var n = IntegerNum.substr(i, 1);
           var p = IntLen - i - 1;
           var q = p / 4;
           var m = p % 4;
            if (n == "0") {
                zeroCount++;
            } else {
                if (zeroCount > 0) {
                    ChineseStr += cnNums[0];
                }
                zeroCount = 0; //  
                ChineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
            }
            if (m == 0 && zeroCount < 4) {
                ChineseStr += cnIntUnits[q];
            }
        }
        ChineseStr += cnIntLast;
    }
    if (DecimalNum != '') {//  
       var decLen = DecimalNum.length;
        for (i = 0; i < decLen; i++) {
            n = DecimalNum.substr(i, 1);
            if (n != '0') {
                ChineseStr += cnNums[Number(n)] + cnDecUnits[i];
            }
        }
    }
    if (ChineseStr == '') { 
        ChineseStr += cnNums[0] + cnIntLast;
    }
    if(ChineseStr.charAt(ChineseStr.length - 1)==cnIntLast){
        ChineseStr+=cnInteger
    }
    return ChineseStr;
}
function rmbToNum(rmb){
    var num="";
    rmb=rmb.indexOf('')==rmb.length-1?rmb+'':rmb;
    rmb=rmb.replace('','');
    var cnNums = new Array("", "", "", "", "", "", "", "", "", ""); //  
    for(var i=0;i<rmb.length;i++){
        if(cnNums.indexOf(rmb[i])>-1){
            num=num+cnNums.indexOf(rmb[i])
        }
        if(rmb.indexOf('')>-1 && rmb[i]==''){
            num=num+'0';
        }
        if(rmb[i]=='' && rmb.indexOf('')==-1 &&  rmb.indexOf('')!=rmb.length-1 && num.indexOf('.')==-1){//
            num=num+'.';
        }
    }
    return num;
}


