<template>
    <v-tab :items="tabItems">
        <div slot="main">   
            <div class="layui-form-item layui-form-text">
                <label class="layui-form-label"></label>
                <div class="layui-input-block">
                    <textarea v-model="beforeTxt" placeholder="..." class="layui-textarea"></textarea>
                </div>
            </div>
            <div class="layui-form-item layui-form-text">
                <label class="layui-form-label"></label>
                <div class="layui-input-inline">
                     <v-select v-model="selectRule" :options="selectOptions"></v-select>
                </div>
                <div class="layui-input-inline">
                    <span style="height:38px;line-height:38px;">_</span>
                </div>
                <div class="layui-input-inline">
                    <input type="text" class="layui-input" v-model="r2" placeholder=""/>
                </div>
            </div>
            <div class="layui-form-item layui-form-text">
                <div class="layui-input-block">
                    <v-button @click="replaceTxt()">
                        <span> <i class="layui-icon">&#xe61a; </i></span>
                    </v-button>
                </div>
            </div>
            <div class="layui-form-item layui-form-text">
                <label class="layui-form-label"></label>
                <div class="layui-input-block">
                    <textarea v-model="afterTxt" placeholder="..." class="layui-textarea"></textarea>
                </div>
            </div>
        </div>
        </v-tab>
</template>
<script>
    export default {
        data() {
            return {
                tabItems:[
                    {
                        Name:'main',
                        Title:''
                    },
                    {
                        Name:'code',
                        Title:'',
                        Url:'http://coding.net/u/yimocoding/p/metools/git/blob/master/src/pages/strsplit.vue'
                    }
                ],
                beforeTxt:'',
                afterTxt:'',
                selectRule:'space',
                r2:'',
                selectOptions:[
                    {
                        Text:'',
                        Value:'space'
                    },
                    {
                        Text:'',
                        Value:'enter'
                    }
                ],
            }
        },
        mounted(){
            // layui.code({
            //     title:'javascript',
            //     encode: true
            // });
        },
        methods: {
            replaceTxt(){
                var str=this.beforeTxt;
                var r1=this.selectRule;
                var r2=this.r2;
                console.log(r1);
                if(r1=='space'){
                    str = str.replace(/\ +/g, r2);
                    str = str.replace(/[ ]/g, r2); 
                }
                else if(r1=='enter'){
                    str=str.replace(/\r\n/g,r2)  
                    str=str.replace(/\n/g,r2)  
                }
                this.afterTxt=str
            }
        }
    }

</script>


