<template>
    <v-tab :items="tabItems">
        <div slot="generatecode">
            <v-input-txt label="/Url" placeholder="" v-model="dataTxt"></v-input-txt>
            
            <div class="layui-form-item layui-form-text">
                <label class="layui-form-label"></label>
                <div class="layui-input-inline">
                    <input type="text" class="layui-input" placeholder=" #ffffff" v-model="color"/>
                </div>
                <div class="layui-input-inline">
                    <input type="text" class="layui-input" placeholder=" #1aa094" v-model="bgcolor"/>
                </div>
            </div>
            <div class="layui-input-block">
                <v-button @click="convert(dataTxt,color,bgcolor)"></v-button>
            </div>
            <div class="layui-input-block">
                <img :src="qrcodeImg" />
            </div>
        </div>
    </v-tab>
</template>
<script>
import jrQrcode from 'jr-qrcode';
export default {
    data(){
        return {
            tabItems:[
                {
                    Name:'generatecode',
                    Title:''
                },
                {
                    Name:'source',
                    Title:'jrQrcode',
                    Url:'https://github.com/diamont1001/jrQrcode'
                },
                {
                    Name:'code',
                    Title:'',
                    Url:"http://coding.net/u/yimocoding/p/metools/git/blob/master/src/pages/qrcode.vue"
                }
            ],
            qrcodeImg:'',
            dataTxt:'',
            color:'',
            bgcolor:'',
        }
    },
    created(){
    },
    watch:{
        'dataTxt':'zidong'
    },
    methods:{
        zidong(){
            this.convert(this.dataTxt,this.color,this.bgcolor);
        },
        convert(txt,color,bgcolor){
            var imgBase64 = jrQrcode.getQrBase64(txt,{
                padding		: 10,   //10px
                width		: 256,  //256px
                height		: 256,  //256px
                correctLevel	: QRErrorCorrectLevel.H,    //level
                background      : color||"#ffffff",    //
                foreground      : bgcolor||"#1aa094"     //
            });
            this.qrcodeImg=imgBase64;
        }
    }

}
</script>


